import {
    registrationPending, // Akce pro signalizaci probhajc registrace
    registrationSuccess, // Akce pro signalizaci spn registrace
    registrationError, // Akce pro signalizaci chyby pi registraci
} from "./userRegSlice"; // Importovn definic akc pro registraci

// Importovn funkce pro registraci uivatele z API
import { userRegistration } from "../../../api/UserApi";

export const newUserRegistration = (frmDt) => async (dispatch) => {
    try {
        dispatch(registrationPending()); // Dispatch akce signalizujc probhajc registraci

        const result = await userRegistration(frmDt);  // Voln funkce pro registraci uivatele z API
        result.status === "success" // Pokud registrace probhla spn
            ? dispatch(registrationSuccess(result.message)) // Dispatch akce signalizujc spnou registraci
            : dispatch(registrationError(result.message));  // Dispatch akce signalizujc chybu pi registraci

        console.log(result);
    } catch (error) {
        dispatch(registrationError(error.message)); // Dispatch akce signalizujc chybu pi registraci
    }
};